@import "../../config/_colors.scss";
@import "../../config/_sizes.scss";
@import "../../config/_variables.scss";
@import "../../config/_mixins.scss";

.container {
  .spinner {
    height: 29px;
    animation: spinner 1.2s steps(12) infinite;
  }
}
 @keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


